<template>
    <i
        class="fa ui-icon" :class="[icon]" v-title="removeText ? null : icon"
        aria-hidden="true"
    ></i>
</template>

<script>
export default {
    name: 'ui-icon',

    props: {
        icon: {
            type: String,
            required: true
        },
        removeText: {
            type: Boolean,
            default: false
        }
    }
};
</script>

<style lang="stylus">
@import './styles/imports';

.ui-button-icon {
    .fa {
        font-size: 1.5em;
    }
}

.ui-icon {
    font-size: 24px;
    width: 1em;
    height: 1em;
    display: inline-block;
    cursor: inherit;
    vertical-align: middle;
}

</style>



